  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 99;
}
.cascader{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
