  background-color: #fff;
  border-radius: 16px 16px 0 0;
  display: flex;
  flex-direction: column;
  z-index: 999;
  .title{
    position: relative;
    padding: 20px 0;
    font-size: 18px;
    line-height: 18px;
    text-align: center;
    .back{
      position: absolute;
      top: 19px;
      left: 20px;
      width: 20px;
      height: 20px;
    }
    .arrow{
      // background: url('./images/icon-arrow.png') no-repeat;
      background-size: 100%;
    }
    .cross{
      // background: url('./images/icon-close.png') no-repeat;
      background-size: 100%;
    }
  }
  .nav-list{
    position: relative;
    display: flex;
    padding: 0 20px;
    font-size: 16px;
    text-align: center;
    .nav-list-item{
      padding: 14px 0;
      margin-right: 16px;
      max-width: 64px;
    }
    .nav-list-item:last-child{
      margin-right: 0;
    }
    .active{
      position: relative;
      color: #1752ff;
    }
    .active::after{
      position: absolute;
      left: 0;
      bottom: 0;
      display: block;
      content: '';
      width: 100%;
      height: 3px;
      background-color: #1752ff;
    }
    .more-hidden{
      align-items: center;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  .select-list{
    flex: 1;
    overflow-y: auto;
    font-size: 16px;
    .select-list-item{
      position: relative;
      padding: 12px 20px;
    }
  }
  .line::after{
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    content: '';
    width: 100%;
    height: 1px;
    background-color: #ccc;
    transform: scaleY(0.5);
  }
  .select-list::-webkit-scrollbar{
    display: none;
  }
}
.fade-enter,.fade-leave-to{
  opacity: 0;
  transform: translateY(100%);
}
.fade-enter-active,.fade-leave-active{
  transition: all 0.3s ease;
}
</style>